<template>
  <div class="glass-bg box-show" style="width:100%; height:58px; top:0; left:0; position:fixed; box-sizing:border-box; border-radius:0;">

    <!-- 用户菜单按钮 -->
    <div class="super-btn-out" @click="changeUserMenuShow" style="width:36px; height:36px; top:8px; left:8px; position:absolute;">
      <span class="super-btn-in MyIF theme" style="width:28px; height:28px; line-height:28px; font-size:21px;"></span>
    </div>

    <!-- 搜索按钮 & 搜索框 -->
    <router-link class="super-btn-out" to="/m/search" v-if="this.$route.path !== '/m/search'" style="width:36px; height:36px; top:8px; right:8px; position:absolute;">
      <span class="super-btn-in MyIF search" style="width:28px; height:28px; line-height:28px; font-size:21px;"></span>
    </router-link>
    <div v-else style="width:100%; height:42px; margin-top:6px; padding-left:58px; float:right; box-sizing:border-box;">
      <div class="glass-bg box-show" style="width:98%; height:100%; position:relative;">
        <label style="width:100%; height:100%; padding:5px 42px 5px 6px; display:inline-block; box-sizing:border-box;">
          <input type="text" style="width:100%; height:100%; box-sizing:border-box; background:#2E2E2E; font-size:18px; color:#BBB;"/>
        </label>
        <span class="super-btn-out" style="width:30px; height:30px; top:5px; right:5px; position:absolute;">
          <span class="super-btn-in MyIF search" style="width:23px; height:23px; line-height:23px; font-size:17px;"></span>
        </span>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'nav-menu',

  methods: {
    changeUserMenuShow () {
      this.$store.commit('changeUserMenuShow')
    }
  }
}
</script>

<style scoped>

</style>



